import moment from 'moment';
import 'moment/locale/cs';
import * as React from 'react';
import { Event } from '../model/database/Event';
import { capitalizeFirstLetter } from '../utils/capitalizeFirstLetter';

interface EventDateComponentProps {
    event: Event;
}

export function EventDateComponent({ event }: EventDateComponentProps) {
    const { date, year, month } = event;

    if (date) {
        try {
            moment.locale('cs');
            let dateString = moment(date).format('LLLL');
            dateString = dateString.split('00:00')[0];
            //TODO: More elegant way
            dateString = dateString.replace('leden', 'ledna');
            dateString = dateString.replace('nor', 'nora');
            dateString = dateString.replace('bezen', 'bezna');
            dateString = dateString.replace('duben', 'dubna');
            dateString = dateString.replace('kvten', 'kvtna');
            dateString = dateString.replace('erven', 'ervna');
            dateString = dateString.replace('ervnaec', 'ervenec'); // This is typo in moment/locale/cs library. We can make an pull request to it but this is the quick local hotfix.
            dateString = dateString.replace('ervenec', 'ervence');
            dateString = dateString.replace('srpen', 'srpna');
            dateString = dateString.replace('z', 'z');
            dateString = dateString.replace('jen', 'jna');
            dateString = dateString.replace('listopad', 'listopadu');
            dateString = dateString.replace('prosinec', 'prosince');
            dateString = capitalizeFirstLetter(dateString);
            return <>&nbsp;{dateString}</>;
        } catch (error) {
            console.error(`Problem when parsing moment('${date}').format('LLLL'); See more below:`);
            console.error(error);
            return <></>;
        }
    } else if (year && month) {
        let monthString = moment(month, 'M').format('MMMM');
        monthString = capitalizeFirstLetter(monthString);
        monthString = monthString.replace('ervnaec', 'ervenec'); // This is typo in moment/locale/cs library. We can make an pull request to it but this is the quick local hotfix.
        return (
            <>
                &nbsp;{monthString} {year}
            </>
        );
    } else {
        return <></>;
    }
}
